import React, {Component} from 'react';
import StravaTable from "../StravaTable/StravaTable";
import './RajDistance-style.css';

class RajDistance extends Component {
    constructor(props) {
        super(props);
        this.state = {
            activities: [],
            users: [],
            distances: {},
            month: "",
            monthNumber: "",
        }
    }

    async fetchData(clientID, secret, refreshToken) {
        const authLink = "https://www.strava.com/oauth/token";
        let token = "";

        await fetch(authLink,{
            method: 'post',
            headers: {
                'Accept': 'application/json, text/plain, */*',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                client_id: clientID,
                client_secret: secret,
                refresh_token: refreshToken,
                grant_type: "refresh_token",
            })
        }).then(res => res.json())
            .then(res => token = res.access_token);

        const activitiesLink = "https://www.strava.com/api/v3/athlete/activities?per_page=200&access_token=" + token;
        await fetch(activitiesLink)
            .then(res => res.json())
            .then(res => {
                const activities = [...this.state.activities];
                const newActivities = activities.concat(res);
                this.setState({
                    ...this.state,
                    activities: newActivities
                })
            });
    }

    setDistance(index) {
        const currentDistances = {run: 40, cycle: 130}
        const lastDistances = {run: 30, cycle: 100}

        if (index === 0) {
            return currentDistances
        } else {
            return lastDistances;
        }
    }

    setMonth(index) {
        const monthList = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
        const date = new Date();
        const currentMonth = monthList[date.getMonth() - index];
        const monthNumber = 1 - index;
        const distance = this.setDistance(index);

        this.setState({
            ...this.state,
            month: currentMonth,
            monthNumber: monthNumber,
            distances: distance,
        })
    }

    async reAuthFunc() {
        await this.setMonth(0);
        await this.setUsers();

        const users = this.state.users;

        users.forEach(user => {
           this.fetchData(user.info.id, user.info.secret, user.info.refresh);
        });
    }

    setUsers() {
        const users = [
            {
                name: "Raj",
                athleteID: "59236473",
                info: {
                    id: "48974",
                    secret: "4175dc3bd00b8a4ce14886912b653faa6b041b2c",
                    refresh: "1bd817b355142a3fb162f46d2ee0e221f20fe315",
                }
            },
            {
                name: "Ross",
                athleteID: "53092595",
                info: {
                    id: "49144",
                    secret: "fd67f97dc6d6d79faf65a179604ffc40337cf879",
                    refresh: "beae09e78a559017ce9b83230b5f014cdefff4f9",
                }
            },
            {
                name: "Cally",
                athleteID: "59236853",
                info: {
                    id: "49145",
                    secret: "a9cd2fef4bcdf252a21aa18fa833cff22fc5fd1a",
                    refresh: "e5f6d38f8d3df4a5ef99ac03fe9ad55f16fa2b3a",
                }
            },
        ];

        this.setState({
            ...this.state,
            users: users
        });
    }

    componentDidMount() {
        this.reAuthFunc();
    }

    findAllSpecificActivity(activityType, athleteID) {

        const activity = [...this.state.activities];
        const monthNumber = this.state.monthNumber;

        const date = new Date();
        const month = date.getMonth() + monthNumber;
        const all = activity.filter(function (element) {
            const stravaDate = element.start_date.substr(5, 2)
            return (element.type === activityType) && (stravaDate == month) && (element.athlete.id == athleteID);
        });

        return all;
    }

    getAllKm(accumulator, a) {
        return Math.round(accumulator + a.distance);
    }

    createUserObj(athleteID, name) {
        const runDistance = this.state.distances.run;
        const bikeDistance = this.state.distances.cycle;

        const userRun = this.findAllSpecificActivity("Run", athleteID);
        const userTotalRan = userRun.length > 0 ? (userRun.reduce(this.getAllKm,0) / 1000) : 0;
        const userRunPercentCapped = userTotalRan > runDistance ? 100 : (userTotalRan / runDistance) * 100;
        const userRunPercent = (userTotalRan / runDistance) * 100;
        const userBike = this.findAllSpecificActivity("Ride", athleteID);
        const userTotalBike = userBike.length > 0 ? (userBike.reduce(this.getAllKm,0) / 1000) : 0;
        const userBikePercentCapped = userTotalBike > bikeDistance ? 100 : (userTotalBike / bikeDistance) * 100;
        const userBikePercent = (userTotalBike / bikeDistance) * 100;
        const userTotalPercent = (userRunPercentCapped + userBikePercentCapped) / 2 === 100 ? (userRunPercent + userBikePercent) / 2 : (userRunPercentCapped + userBikePercentCapped) / 2;
        const userObj = {
            name: name,
            runQuantity: userRun.length,
            runDistance: userTotalRan,
            runPercentage: userRunPercent,
            bikeQuantity: userBike.length,
            bikeDistance: userTotalBike,
            bikePercentage: userBikePercent,
            totalPercent: userTotalPercent,
            allRuns: userRun,
            allCycles: userBike,
        };

        return userObj;
    }

    render() {
        const users = this.state.users;
        const monthList = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
        const date = new Date();
        const lastMonth = monthList[date.getMonth() - 1];
        const thisMonth = monthList[date.getMonth()];
        const month = this.state.month;


        const allRows = users.map(user => {
           return this.createUserObj(user.athleteID, user.name);
        });

        return (
            <div>
                <h2 style={{padding: "20px 20px 0 20px"}}>Boys Dualathalon</h2>
                <button className={this.state.monthNumber === 0 ? "selectedButton" : "nonSelectedButton"} onClick={() => this.setMonth(1)}>{lastMonth}</button>
                <button className={this.state.monthNumber === 1 ? "selectedButton" : "nonSelectedButton"} onClick={() => this.setMonth(0)}>{thisMonth}</button>
                <h4 style={{paddingTop: "10px"}}>{month} Challenge</h4>
                <h6>Run {this.state.distances.run} km  |  Cycle {this.state.distances.cycle} km</h6>
                <StravaTable allRows={allRows} />
            </div>
        )
    }
}

export default RajDistance;
